import React from 'react'

import { useNavigate } from 'react-router-dom'

export const UserInfo = (params) => {
    
    let navigate = useNavigate()

    const {
        login,
        repos_url,
        name,
        avatar,
        location,
        followers,
        following,
        public_repos,
        created_at,
    } = params

    const handleHistoryPush = () => {
        navigate({ pathname: `/user/${login}` })
    }

    return (
        <div>
            {
                public_repos ? (
                    <>
                        <p>Say hi to: {login}</p>
                        <img src={avatar} alt={name} />
                        <p>{location}</p>
                        <p>{followers}</p>
                        <p>{following}</p>
                        <p>{public_repos}</p>
                        <p>{created_at}</p>
                    </>
                ) : (
                    <>
                        <p>{login}</p>
                        <img src={avatar} alt={name} />
                        <p>{location}</p>
                        <p>{followers}</p>
                        <p>{following}</p>
                        <button onClick={handleHistoryPush}>See more</button>
                    </>
                )
            }
        </div>
    )
}
